.customise{
  background-color:$blue-90;
  @include flex-center;
  flex-direction: column;
  padding:10rem 1rem 0;
  text-align: center;

  &-heading{
    font-size: 4rem;
    font-weight: lighter;
    color: $white;
    margin-bottom: 1.7rem;
  }
  &-desc{
    font-size: 1.6rem;
  font-weight: 300;
  line-height: 1.56;
  color: $grey-30;
  margin-bottom: 4.9rem;
  }
  &-btn{
    margin-bottom: 5.4rem;
  }

  .diamond{
    max-height:23rem;
    width: auto;
    margin-bottom:-8rem;
  }
}