// footer section

.footer {
background-color: $blue-100;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  &-info {
    flex: 1.5;
    padding: 12.5rem 1rem 17rem;
    background-color: $blue-90;

    nav {
      ul {
        border-top: 0.3rem solid $blue-70;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        max-width: 56rem;
        margin: 0 auto;
        padding-top: 2.6rem;
        li {
          list-style-type: none;
          a {
            @extend %anchor-link;
            font-weight: 600;
            line-height: 1.5;
          }
          .footer-break {
            display: none;
          }
        }
      }
    }
    p {
      max-width: 56rem;
      margin: 3rem auto 0;

      font-size: 1.3rem;
      font-weight: 300;
      line-height: 1.92;
      color: $white;
      span {
        font-weight: bold;
      }
    }
  }

  &-socials {
    flex: 1;
    padding: 15.4rem 1rem 17rem 7rem;
    background-color: $blue-100;

    h2{
      font-size: 1.3rem;
      font-weight: 600;
      line-height: 1.5;
      text-align: left;
      color: $white;
      margin-bottom: 2.6rem;
    }

    .twitter {
      padding: 2rem 1.8rem 1.5rem;
      background-color: $blue-tw;
      border-radius: 0.3rem;
      margin-right: 2rem;
      margin-bottom: 1rem;
      display: inline-block;
      img {
        height: 1.6rem;
        width: auto;
      }
    }
    .facebook {
      padding: 2.1rem 2.3rem 1.5rem;
      background-color: $blue-fb;
      border-radius: 0.3rem;
      display: inline-block;
      margin-bottom: 1rem;
      img {
        height: 1.6rem;
        width: auto;
      }
    }
  }
}

@media (max-width:992px) {
  .footer {
   align-items: center;
    &-info {
        text-align: center;
        padding: 5rem 1rem;
      nav {
        ul {
          flex-direction: column;
          li{
            text-align: center;
            margin: 0.5rem 0;        
          }
        }
      }
    }
    &-socials{
      height: 100%;
      padding: 5rem 3rem;
    }
  }
}
