.features {
  width: 100%;
  @include flex-top-center;
  flex-direction: row;
  max-width: 106rem;
  margin: 0 auto;
  padding: 10rem 0;

  article {
    flex: 1;
    @include flex-top-center;
    padding: 2rem;
    .article-img {
      height: 4rem;
      min-width: 4rem;
      vertical-align: top;
    }

    .text-container {
      max-width: 28.5rem;
      margin-left: 2.4rem;
      .article-heading{
        font-size: 1.5rem;
        font-weight: 600;
        color:$blue-90;
        margin-bottom: 1rem;
      }
      .article-desc{
        font-size: 1.5rem;
        font-weight: 300;
        line-height: 1.67;
        color: $blue-70;
      }
    }
  }
}


@media (max-width:1220px){
  .features{
    flex-direction: column;
    article{
      .text-container{
        max-width: 60rem;
      }
    }
  }

}