//header section
.header{
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  z-index: 100;
.header-container {
  margin: 0 auto;
  max-width: 106rem;
  @include flex-center;
  justify-content: space-between;
  
  padding: 4.3rem 1rem 3rem;
  border-bottom: 0.2rem solid rgba($white, 0.2);
  transition: all 0.3s;

  .logo {
    height: 2.9rem;
  }

  .nav-bar {
    width: 50%;
    ul {
      li {
        display: inline-block;

        a {
          @extend %anchor-link;
          margin-right: 2rem;
        }
      }
    }
  }

  .login-signup {
    a {
      @extend %anchor-link;
      margin-right: 1rem;
      display: inline-block;

      &:nth-child(2) {
          &:hover{
            color:$dark-background;
          }
      }
    }
  }
  .burger-menu-icon{
    cursor: pointer;

    display: none;
    .bar{
      display: block;
      width: 3.5rem;
      height: 0.5rem;
      margin: 0.7rem auto;
      background-color: $white;
      border-radius: 0.5rem;
      transition: all 0.3s ease-in-out;    
    }
  }
}
}

.burger-menu{
  display: none;
  padding: 12rem 3rem 2rem;
  background-color: $dark-background;
  height: 100vh;
  &-nav-bar{
    ul {
      li {
        list-style-type: none;
        margin: 3rem 0;
        a {
          @extend %anchor-link;
          font-size: 2.5rem;
          text-align: justify;
          margin-right: 2rem;
        }
      }
    }
  }
  
  &-login-signup{
    a {
      @extend %anchor-link;
      font-size: 2.5rem;
      margin-right: 1rem;
      display: inline-block;
    }
  }
}

.scroll{
  background-color: $blue-100;
  transition: all 0.3s;
}

@media (max-width: 992px) {
  .header{
  .header-container {
    .nav-bar {
      display: none;
    }
    .login-signup {
      display: none;
    }
    .burger-menu{
      display: inline-block;
    }
    .burger-menu-icon{
      display: block;
    }
  }
  }
  .header-active{
    position: static;
    background-color:$dark-background;
  }

  .burger-menu{
    display: block;
  }

  .burger-menu-icon.active{
    .bar:nth-child(2) {
    opacity: 0;
    }

    .bar:nth-child(1){
      transform: translateY(1.2rem) rotate(45deg);
    }

    &:hover{
      .bar:nth-child(1){
      transform: translateY(1.2rem) rotate(45deg);
      }
    }

    .bar:nth-child(3){
      transform: translateY(-1.2rem) rotate(-45deg);
    }

    &:hover{
      .bar:nth-child(3){
      transform: translateY(-1.2rem) rotate(-45deg);
      }
    }
  }
  
  
  .burger-menu-icon:hover{
    .bar:nth-child(1) {
    transform: translateY(0.2rem) rotate(4deg);
    }
    .bar:nth-child(3) {
    transform: translateY(-0.2rem) rotate(-4deg);
  }

}
}