.news {
  text-align: center;
  padding-bottom: 12rem;
  &-articles {
    @include flex-center;
    flex-direction: row;
    .container {
      max-width: 60rem;
      padding: 2rem 0;
      text-align: center;
      flex: 1;

      img {
        width: 100%;
      }

      &-heading {
        font-size: 1.1rem;
        font-weight: 600;
        color: $grey-30;
        margin: 3.4rem 0 0.6rem;
      }

      &-desc {
        font-size: 1.5rem;
        font-weight: 300;
        line-height: 2;
        color: $grey-50;
      }
    }
  }

  &-btn {
    margin-top: 6rem;
    font-weight: 600;
  }
}

@media (max-width:1220px) {
  .news {
    padding:2rem;
    .news-articles {
      flex-direction: column;
    }
  }
}
