.login{
  padding: 12rem 0 13.8rem;
  @include flex-center;
  flex-direction: column;
  
  &-heading{
    font-weight: 100;
    font-size: 2.4rem;
    color: $blue-90;
    padding: 0.5rem;
  }

  &-desc{
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 2;
    color: $grey-50;
    margin-bottom:5.5rem;
  }
  .login-credential{
    border:0.1rem solid $blue-5;
    padding:1.2rem 2.2rem;
    border-radius: 0.5rem;
    margin-bottom:1rem;
    width: 40rem;
    
    .icon{
      margin:0.5rem 1.5rem 0 0;
      width: 1.2rem;
      height: auto;
    }
    .input{
      padding:0.5rem 0 0.5rem 1.8rem;
      border: none;
      outline: none;
      color: $grey-30;
      border-left: 0.1rem solid $blue-5;
      width: 32rem;

      &::placeholder{
        color: $grey-30;
      }
    }

  }
  
  &-btn{
    width: 40rem;
  }


}