*,*::after,*::before{
  padding:0;
  margin:0;
  box-sizing: border-box;
  font-family: sans-serif;
}

html{
  font-size: 62.5%;
}

%anchor-link{
  text-decoration: none;
  font-size: 1.3rem;
  color: $white;
}



@media (max-width:992px) {
  .display-none{
    display: none !important;
  } 
}

@media(max-width:480px){
  html{
    font-size: 40%;
  }
}



