.product-features {
  background-color: $blue-5;
  // max-width: 1600px;
  margin: 0 auto;
  padding-top: 11.8rem;
  font-size: 0;
  @include flex-top-center;
  
  .img-container {
    position: relative;
    display: inline-block;
    width: 40%;
    .tab {
      display: inline-block;
      max-width: 54.7rem;
      width: 100%;
      height: auto;
    }
    .phone {
      position: absolute;
      right:0%;
      bottom: 0;
      max-width: 30.4rem;
      width: 100%;
      height: auto;
    }
  }

  .container{
    height: 100%;
    @include flex-center;
    flex-direction: column;
    width: 60%;
    z-index: 2;
  .desc-container {
    font-size: 1.6rem;

    &-heading {
      font-size: 2.4rem;
      color: $blue-90;
      margin-bottom: 2rem;
    }
    &-desc {
      font-size: 1.5rem;
      font-weight: 300;
      max-width: 41rem;
      line-height: 1.67;
      color: $blue-70;
      margin-bottom: 6rem;
    }
    
    .article {
      flex: 1;
      @include flex-top-center;
      padding: 2rem 0;
      &-Img {
        height: 3.5rem;
        min-width: 3.5rem;
        vertical-align: top;
        margin-right:2rem;
        
      }
      .text-container {
        max-width: 28.5rem;
        margin-left: 2.4rem;
        .article-heading{
          font-size: 1.5rem;
          font-weight: 600;
          color: $blue-90;
          margin-bottom: 1rem;
        }
        .article-desc{
          font-size: 1.5rem;
          font-weight: 300;
          line-height: 1.67;
          color: $blue-70;
          max-width: 27.5rem;
        }
      }
      
    }
    article:nth-child(3),article:nth-child(4){
      border-bottom:0.1rem solid $blue-5;
    }
    
  }
}
}

@media (max-width:992px) {

  .product-features {
    flex-direction: column-reverse;
    padding: 5rem 0 0;
    gap: 5rem;
    .container{
      margin: 0 auto;
    }

    .img-container{
      width: 100%;
      max-width: 60rem;
      .phone{
      position: absolute;
      right: 0;
      bottom: 0;
      width: 100%;
      height: auto;
      }
    }
  }
  
}

@media (min-width:2000px) {
  .product-features {
    .img-container {
      .phone {
        left: 400px;
        
      }
    }
}

}