.showcase{
  padding:18rem 2rem 11rem;
  max-width: 106rem;
  margin:0 auto;
  font-size: 0;
  
  &-heading{
    font-size: 2.4rem;
    color:$blue-90;
    margin-bottom: 8.2rem;
  }

  article{
    display: inline-block;
    width: 50%;
    font-size: 1.6rem;
    padding: 2rem;

    .sa-heading{
      font-size: 1.5rem;
      font-weight: 600;
      color:$blue-90;
      margin-bottom: 2rem;
    }
    .sa-desc{
      font-size: 1.5rem;
      font-weight: 300;
      line-height: 2;
      color:$blue-70;
      max-width: 42.1rem;
    }

  }

}

@media (max-width:600px) {
  .showcase{
    padding-bottom:2rem;

    &-heading{
      margin-bottom:4rem;
    }
    article{
      display: block;
      width: 100%;
    }
  }
}