
.btn-primary{

  @extend %anchor-link;
  color: $btn-primary;
  display: inline-block;
  border: 0.2rem solid $btn-primary;
  padding: 1.8rem 2.5rem;
  border-radius: 4rem;
  transition: ease-in-out 0.3s;
  margin: 0 1rem;

  &:hover{
    background-color:$btn-primary;
    color:$white;
  }
  
}

.btn-secondary{
  display: inline-block;
  border: 0.2rem solid $white;
  padding: 0.7rem 2.9rem;
  border-radius: 4rem;
  transition: ease-in-out 0.3s;
  color: $white;

   &:hover {
        color:$dark-background;
         background-color: $white;
   }

}


.btn-primary-login{
    @extend %anchor-link;
    background-color:$btn-primary;
    padding: 1.9rem 1rem;
    text-align: center;
    border-radius: 0.5rem;
    transition: 0.3s;
    border: 0.1rem solid $btn-primary;

    &:hover{
      background-color:$white;
      color: $btn-primary;
    }
}
