.design-features{
  margin:0 auto;
  @include flex-center;
  padding: 13rem 0;

  .container{
    padding:2rem;
    flex:1;
    @include flex-top-center;


    .desc-container{
      
    &-heading{
      text-align: left;
      font-size: 2.4rem;
      color:$blue-90;
      margin-bottom:2rem;
    }
    &-desc{
      font-size: 1.5rem;
      font-weight: 300;
      line-height: 1.67;
      color: $blue-70;
      margin-bottom:3rem;
      max-width: 49rem;
    }
    .feature-btn{
      font-weight: 600;
      padding: 1rem 2.5rem;
        }
      }
  
    }

    .img-container{
      flex: 1;
      text-align: right;
      .laptop{
        max-width: 73.7rem;
        width: 100%;
      
      }
    }

  }

  @media (max-width:992px) {

    .design-features{
      gap:5rem;
      flex-direction: column;
    .img-container{
      width: 100%;
      .laptop{
      max-width: 60rem;
      }
    }
    }

  }
  
