.slider{
  position: relative;
  .carousel{
  background-image: url('../assets/carousel/carouselImg.png');
  width: 100%;
  height: 50rem;
  background-size: cover;
  background-position: center;
  @include flex-center;

  .slide{
    text-align: center;
    color: $white;
    padding:0 5rem;
    
    &Img{
      height:8rem;
      width: 8rem;
    }

    &-heading{
      font-size: 4rem;
      font-weight: 300;
      font-style: italic;
      color: $white;
      margin: 2.8rem 0;
    }

    &-desc{
      font-size: 1.5rem;
      font-weight: 300;
      letter-spacing: 0.2rem;
      color: $grey-30;
    }


  }

}

}


.prev, .next {
  cursor: pointer;
  background:none;
  border: none;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 1.6rem;
  margin-top: -2.2rem;
  color: $white;
  font-weight:100;
  font-size: 5rem;
  transition: 0.3s;
  border-radius: 0 0.3rem 0.3rem 0;
  user-select: none;
}

.prev{
  left:0;
}
.next {
  right: 0;
  border-radius: 0.3rem 0 0 0.3rem;
}

.prev:hover, .next:hover {
  background-color: rgba($black,0.8);
}


.dots{
  text-align: center;
  position: absolute;
  bottom:1rem;

  .dot {
    cursor: pointer;
    height: 1.5rem;
    width: 1.5rem;
    margin: 0 0.2rem;
    background-color: $grey-50;
    border-radius: 50%;
    display: inline-block;
  }
  
  .active, .dot:hover {
    border: 0.3rem solid $white;
  }
}


