.banner{
  width: 100%;
  height: auto;
  background-image: url("../assets/bannerAssets/banner-bg-img.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: darken;
  background-color: rgba($dark-background, 0.9);
  text-align: center;
  padding:25rem 2rem 3rem;

  h1{
    font-size: 6rem;
    font-weight: normal;
    color:$white;
    margin-bottom: 1.5rem;
  }
  p{
    max-width:82.9rem;
    margin: 0 auto;
    font-size: 2rem;
    font-weight:100;
    color:$white;
    margin-bottom: 8.4rem;
  }
  a{
    margin:1rem;
    width: 20.9rem;
  }

  .banner-arrow{
    display: block;
    margin: 15.8rem auto 0;
    height: 2rem;
    cursor: pointer;
  }

}